<template>
	<div class="brand" :class="{'brand--dark' : isdark}">
		<router-link to="/" class="brand__link">
			<span class="brand__link-part" >
				<span class="brand__text">solo</span>
				<div class="brand__foreground"></div>
			</span>
			<span class="brand__link-part">
				<span class="brand__text">mon</span>
			</span>
		</router-link>
	</div>
</template>

<script>
	export default {
		name: 'Brand',
		props: ['isdark', 'preloader'],
		data(){
			return {

			}
		},
		mounted() {
			this.brand_onLoad()
		},
		methods: {
			brand_onLoad(){
				let brand_text = document.querySelectorAll('.brand__text'),
					brand__foreground = document.querySelector('.brand__foreground');

				brand_text.forEach((text) => {
					text.classList.add('active')
				})
				brand__foreground.classList.add('active')
			}
		}
	}
</script>