</template>
<script>
	import Navigation from '@/components/Navigation.vue' 
	import Brand from '@/components/Brand.vue'
	export default {

		name: 'Projects',
		components: {
			Navigation, Brand
		},
		props: ['apploaded', 'preloader'],
		created(){
			this.set_page_title()
		},
		watch: {
			preloader: function(value) {
				if(value){
					this.animate_first_entrance()
				}
			}
		},
		mounted(){
			if(this.preloader){this.animate_first_entrance()}
			this.set_page_title()
			this.animate_onScroll()
			
		},
		methods: {
			set_page_title(){
				document.title = 'Andrew Solomon | Projects'
			},
			animate_first_entrance(){
				let animated_text = document.querySelectorAll('.animated-text')
				animated_text.forEach((elem)=> {
					if(elem.getBoundingClientRect().top < window.innerHeight / 100 * 75){
					elem.classList.add('animated-text--active')
					}
				})

				let animated_foreground_x = document.querySelectorAll('.animated-foreground--horizontal')
				animated_foreground_x .forEach((elem)=> {
					if(elem.getBoundingClientRect().top < window.innerHeight / 100 * 75){
					elem.classList.add('animated-foreground--horizontal--active')
					}
				})

				let projects_foreground = document.querySelectorAll('.projects__foreground')
				projects_foreground .forEach((elem)=> {
					if(elem.getBoundingClientRect().top < window.innerHeight / 100 * 75){
					elem.classList.add('projects__foreground--active')
					}
				})

				let projects_image = document.querySelectorAll('.projects__image')
				projects_image .forEach((elem)=> {
					if(elem.getBoundingClientRect().top < window.innerHeight / 100 * 75){
					elem.classList.add('projects__image--active')
					}
				})

				let discover_text = document.querySelectorAll('.projects__discover-text')
				discover_text .forEach((elem)=> {
					if(elem.getBoundingClientRect().top < window.innerHeight / 100 * 75){
					elem.classList.add('projects__discover-text--active')
					}
				})


				let discover_foreground = document.querySelectorAll('.projects__discover-foreground')
				discover_foreground .forEach((elem)=> {
					if(elem.getBoundingClientRect().top < window.innerHeight / 100 * 75){
					elem.classList.add('projects__discover-foreground--active')
					}
				})
			},
			animate_onScroll(){
				document.addEventListener('scroll', (e) => {
					this.animate_first_entrance()
				})
			}
		}
	}
</script>