                  </a>
               </li>
            </ul>
			<div class="contact__email" @click="copy_email()">
				<div class="contact__email-value">SolomonAndrew@tutanota.com</div>
				<div class="contact__email-copy">{{ copy_text }}</div>
			</div>
         </div>
	</div>
</template>

<script>

import Navigation from '@/components/Navigation.vue'
import Brand from '@/components/Brand.vue'
import gsap from 'gsap'
	export default {
		name: 'Contact',
		components: {
			Navigation, Brand
		},
		props: ['apploaded', 'preloader'],
		data(){
			return {
				copy_text: 'copy'
			}
		},
		mounted(){
			if(this.preloader){this.animate_first_entrance()}
		},
		watch: {
			preloader: function (value) {
				if(value){
					this.animate_first_entrance()
				}
			}
		},
		methods: {
			animate_first_entrance(){
				document.querySelector('.contact__email-value').classList.add('active')
				gsap.to('.socials__foreground.sf1', {y: '200%', duration: 1.5, delay: 0.3, easing: 'linear'})
				gsap.to('.socials__link.sl1', {opacity: 1, delay: 0.65, duration: 0.1})
			
				gsap.to('.socials__foreground.sf2', {y: '200%', duration: 1.5, delay: 0.4, easing: 'linear'})
				gsap.to('.socials__link.sl2', {opacity: 1, delay: 0.75, duration: 0.1})

				gsap.to('.socials__foreground.sf3', {y: '200%', duration: 1.5, delay:0.5, easing: 'linear'})
				gsap.to('.socials__link.sl3', {opacity: 1, delay: 0.85, duration: 0.1})
			},
			copy_email(){
				navigator.clipboard.writeText('SolomonAndrew@tutanota.com')
				this.copy_text = 'copied'

				setTimeout(()=>{
					this.copy_text = 'copy'
				}, 5000)
			}
		}
	}
</script>

<style lang="sass" scoped>
