<template>
	<nav class="navigation">
		<div class="navigation__button-wrap" @click="toggle_navigation()">
			<button class="navigation__button">
				<div class="navigation__button-square s1"></div>
				<div class="navigation__button-square s2 interact"></div>
				<div class="navigation__button-square s3"></div>
				<div class="navigation__button-square s4 interact"></div>
				<div class="navigation__button-square s5"></div>
				<div class="navigation__button-square s6 interact"></div>
				<div class="navigation__button-square s7"></div>
				<div class="navigation__button-square s8 interact"></div>
				<div class="navigation__button-square s9"></div>
			</button>
		</div>
		<div class="navigation__wrap">
			<div class="navigation__wrap-item wi1"></div>
			<div class="navigation__wrap-item wi2"></div>
			<div class="navigation__wrap-item wi3"></div>
			<div class="navigation__wrap-item wi4"></div>
			<div class="navigation__wrap-item wi5"></div>
			<div class="navigation__wrap-item wi6"></div>
			<div class="navigation__wrap-item wi7"></div>
			<div class="navigation__wrap-item wi8"></div>
			<div class="navigation__wrap-item wi9"></div>
			<div class="navigation__content">
				<div class="navigation__main-title">
					<span class="navigation__main-title-text">
						{{ this.nav_title }}
					</span>
				</div>
				<ul class="navigation__list" >
					<li class="navigation__item"><router-link to="/" class="navigation__link nl1" @mouseover="change_nav_title('home')" @mouseout="remove_nav_title();"><span class="navigation__link-text">home</span><span class="navigation__link-foreground nf1"></span></router-link> </li>
					<li class="navigation__item"><router-link to="/about"  class="navigation__link nl2" @mouseover="change_nav_title('about')" @mouseout="remove_nav_title();"><span class="navigation__link-text">about</span><span class="navigation__link-foreground nf2"></span></router-link></li>
					<li class="navigation__item"><router-link to="/services" class="navigation__link nl3" @mouseover="change_nav_title('services')" @mouseout="remove_nav_title();"><span class="navigation__link-text">services</span><span class="navigation__link-foreground nf3"></span></router-link></li>
					<li class="navigation__item"><router-link to="/projects" class="navigation__link nl4" @mouseover="change_nav_title('projects')" @mouseout="remove_nav_title();"><span class="navigation__link-text">projects</span><span class="navigation__link-foreground nf4"></span></router-link> </li>
					<li class="navigation__item"><router-link to="/contact" class="navigation__link nl5" @mouseover="change_nav_title('contact')" @mouseout="remove_nav_title();"><span class="navigation__link-text">contact</span><span class="navigation__link-foreground nf5"></span></router-link></li>
				</ul>
				<div class="navigation__copyright">
					<span class="navigation__copyright-text"> © Andrew Solomon, 2021</span>
					<span class="navigation__copyright-foreground"></span>
				</div>
				<ul class="navigation__locale-list">
					<li class="navigation__locale-item navigation__locale-item--active">en</li>
				</ul>
			</div>
		</div>
		<Statistics></Statistics>
	</nav>
</template>

<script>
	import gsap from "gsap";
	import Statistics from '@/components/Statistics.vue';
	export default {
		name: 'Navigation',
		components: {
			Statistics
		},
		data() {
			return {
				nav_title: 'menu',
				nav_is_active: false,
				nav_click_allowed: true,
				nav_animation: null,
			}
		},
		
		mounted(){
			
			this.animate_first_entrance()	
		},
		methods: {
			animate_first_entrance(){
				window.scrollTo(0, 0)
				gsap.to('.navigation__button', {height: '60px', delay: 0, duration: 0.45})
				let squares = document.querySelectorAll('.navigation__button-square')
					squares.forEach((square) => {
						square.classList.add('loaded')
					})
			},
			change_nav_title(title){
				gsap.to('.navigation__main-title-text', {y: '-110%', duration: 0.3});
				gsap.to('.navigation__main-title-text', {y: 0, delay: 0.3, duration: 0.35});
				setTimeout(()=>{this.nav_title = title}, 300)
			},
			remove_nav_title(){
				gsap.to('.navigation__main-title-text', {y: '-110%', duration: 0.3});
				gsap.to('.navigation__main-title-text', {y: 0, delay: 0.3, duration: 0.35});
				setTimeout(()=>{this.nav_title = 'menu'}, 300)
			},
			show_navigation(){

			},
			toggle_navigation(){
				this.nav_is_active = !this.nav_is_active
				let interact_squares = document.querySelectorAll('.navigation__button-square.interact'),
					wrap_items = document.querySelectorAll('.navigation__wrap-item'),
					nav_content = document.querySelector('.navigation__content'),
					nav_wrap = document.querySelector('.navigation__wrap');
				
				if (this.nav_is_active){

					interact_squares.forEach((square) => {
						square.classList.add('active')
					})
					wrap_items.forEach((item) => {
						item.classList.add('active')
					})
					nav_content.classList.add('navigation__content--active')



					gsap.fromTo('.navigation__main-title-text', {y: '150%'}, {y: '0', duration: 0.5, delay: 1.2})
					gsap.from('.nl1', {duration: 0.28, delay: 1.1, scale: 0.5})
					gsap.from('.nl2', {duration: 0.3, delay: 1.2, scale: 0.5})
					gsap.from('.nl3', {duration: 0.32, delay: 1.3, scale: 0.5})
					gsap.from('.nl4', {duration: 0.34, delay: 1.4, scale: 0.5})
					gsap.from('.nl5', {duration: 0.36, delay: 1.5, scale: 0.5})


					// Navigation foreground
					gsap.from('.nf1', {duration: 0.18, delay: 1.1, width: 0})
					gsap.from('.nf2', {duration: 0.2, delay: 1.2, width: 0})
					gsap.from('.nf3', {duration: 0.22, delay: 1.3, width: 0})
					gsap.from('.nf4', {duration: 0.24, delay: 1.4, width: 0})
					gsap.from('.nf5', {duration: 0.26, delay: 1.5, width: 0})

				}
				else {

					// Removing navigation button animation
					interact_squares.forEach((square) => {
						square.classList.remove('active')
					})

					// Removing navigation button animation
					wrap_items.forEach((item) => {
						item.classList.remove('active')
					})
					nav_content.classList.remove('navigation__content--active')
					this.nav_title = 'menu'
	
				}
				return;
			}
		}
	}
</script>