<template>
	<div class="preloader">
      <div class="preloader__column preloader__left"></div>
      <div class="preloader__column preloader__center">
         <div class="preloader__body">
            <div class="preloader__name">
               <div class="preloader__first-name">
                  <div class="preloader__letter a">A</div>
                  <div class="preloader__letter n">n</div>
                  <div class="preloader__letter d">d</div>
                  <div class="preloader__letter r">r</div>
                  <div class="preloader__letter e">e</div>
                  <div class="preloader__letter w">w</div>
               </div>
               <div class="preloader__last-name">
                  <div class="preloader__letter s">S</div>
                  <div class="preloader__letter o">o</div>
                  <div class="preloader__letter l">l</div>
                  <div class="preloader__letter o2">o</div>
                  <div class="preloader__letter m">m</div>
                  <div class="preloader__letter o3">o</div>
                  <div class="preloader__letter n">n</div>
               </div>
            </div>
           <!-- <div class="preloader__progress-bar">Loading...</div> -->
         </div>
      </div>
      <div class="preloader__column preloader__right">
         <div class="preloader__right-top"></div>
         <div class="preloader__right-bottom"></div>
      </div>
   </div>
</template>

<script>
import gsap from 'gsap';
export default {
	name: 'Preloader',
	props: ['apploaded', 'preloader'],
	data(){
		return {
			loaded: false
		}
 	},
	watch: {
		apploaded: function (value) {
			if(value) { this.close_preloader() }
		}
	},
	mounted(){
		// this.close_preloader()
	},
	methods: {
		close_preloader(){
			gsap.to('.preloader__first-name .a', {y: '150%', delay: 0.3, duration: 0.5, opacity: 0})
			gsap.to('.preloader__first-name .n', {y: '150%', delay: 0.4, duration: 0.5, opacity: 0})
			gsap.to('.preloader__first-name .d', {y: '150%', delay: 0.5, duration: 0.5, opacity: 0})
			gsap.to('.preloader__first-name .r', {y: '150%', delay: 0.6, duration: 0.5, opacity: 0})
			gsap.to('.preloader__first-name .e', {y: '150%', delay: 0.7, duration: 0.5, opacity: 0})
			gsap.to('.preloader__first-name .w', {y: '150%', delay: 0.8, duration: 0.5, opacity: 0})

			// Анимруем фамилию на прелоадере
			gsap.to('.preloader__last-name .s', {y: '150%', delay: 0.9, duration: 0.5, opacity: 0})
			gsap.to('.preloader__last-name .o', {y: '150%', delay: 1, duration: 0.5, opacity: 0})
			gsap.to('.preloader__last-name .l', {y: '150%', delay: 1.1, duration: 0.5, opacity: 0})
			gsap.to('.preloader__last-name .o2', {y: '150%', delay: 1.2, duration: 0.5, opacity: 0})
			gsap.to('.preloader__last-name .m', {y: '150%', delay: 1.3, duration: 0.5, opacity: 0})
			gsap.to('.preloader__last-name .o3', {y: '150%', delay: 1.4, duration: 0.5, opacity: 0})
			gsap.to('.preloader__last-name .n', {y: '150%', delay: 1.5, duration: 0.5, opacity: 0})
			gsap.to('.preloader__progress-bar', {scale: 0.5, delay: 1.5, duration: 0.5, opacity: 0})



			// Прячем левую и центральную колонку прелоадера
			gsap.to('.preloader__left', {height: '0', delay: 2, duration: 0})
			gsap.to('.preloader__center', {height: '0', delay: 2, duration: 0})


			// Прячем правую колонку прелоадера
			gsap.to('.preloader__right-top', {height: '49.5%', delay: 2, duration: 0.3})
			gsap.to('.preloader__right-bottom', {height: '49.5%', delay: 2, duration: 0.3})

			gsap.to('.preloader__right-top', {height: 0, delay: 2.3, duration: 0.45})
			gsap.to('.preloader__right-bottom', {height: 0, delay: 2.3, duration: 0.45})


			// Полностью прячем прелоадер
			gsap.to('.preloader', {height: 0, delay: 2.75, duration: 0})


			// gsap.from('.header__link-background', {width: 0, duration: 0.35, delay: 5.95})
			// gsap.from('.header__lnk-text', {y: '200%' , duration: 0.45, delay: 6.15})
			setTimeout(()=> {
				this.$parent.preloader_isHidden = true
			}, 2000)	
		}
	}
}
</script>