<template>
	
	<div class="project">
		<Navigation></Navigation>
		<Brand></Brand>
		
		<div class="project__header">	
			<div class="project__title">
				<div class="animated-container">
					<span class="project__title-text">{{ projects[active_project].title }}</span>
					<span class="project__title-foreground animated-foreground"></span>
				</div>
			</div>
			<div class="project__subtitle">
				<span class="project__subtitle-text">Wedding photographer from LA</span>
				<span class="project__subtitle-foreground"></span>
			</div>
			<hr>
			<div class="project__about">
				<div class="project__description">
					<p class="project__description-text">
						{{ projects[active_project].description }}
					</p>
				</div>
				<div class="project__details">
					<div class="project__stek  ps1">
						<div class="project__stek-title pw1">Technologies</div>
						<div class="project__stek-value value" :key="item.id" v-for="item in projects[active_project].technologies"> <span>{{ item }}</span> </div>
					</div>
					<div class="project__date  ps2">
						<div class="project__date-title pw2">Year</div>
						<div class="project__date-value value"><span>{{ projects[active_project].year }}</span></div>
					</div>
					<!--
					<div class="project__services ps3">
						<div class="project__services-title pw3">Services</div>
						<div class="project__services-value value" :key="item.id" v-for="item in projects[active_project].services"><span>{{ item }}</span></div>
					</div>
					-->
					<div class="project__link ps4" v-if="projects[active_project].link">
						<div class="project__link-title pw4">Live</div>
						<div class="project__link-value value" style="text-decoration: underline;"><a :href="projects[active_project].link" target="_blank">{{ projects[active_project].title }}</a></div>
					</div>
				</div>
				<div class="project__navigation project__navigation--top">
					<button @click="route_to_page('prev')" class="project__navigation-previous project__navigation-button">
						<span class="project__navigation-text" >previous project</span>
						<span class="project__navigation-foreground"></span>
					</button>
					<button @click="route_to_page('next')" class="project__navigation-next project__navigation-button">
						<span class="project__navigation-text">next project</span>
						<span class="project__navigation-foreground project__navigation-foreground--left"></span>
					</button>
				</div>
			</div>
		</div>
		
		<div class="project__content" v-if="projects[active_project].portfolio">
			<div class="project__content-part" :key="item" v-for="(item, index) in projects[active_project].portfolio">
				<div class="project__page-title">
					<div class="animated-title">
						<div class="project__page-title-text">{{ item.title }}</div>
					</div>
				</div>
				<div class="project__image-wrap" :key="src" v-for="src in projects[active_project].portfolio[index].images">
					<span class="project__image-foreground"></span>
					<img :src="require(`../assets/img/projects/${src}`)" alt="">
				</div>
				
			</div>
		</div>
		<div class="project__navigation" v-if="projects[active_project].portfolio">
			<button @click="route_to_page('prev')" class="project__navigation-previous project__navigation-button">
				<span class="project__navigation-text" >previous project</span>
				<span class="project__navigation-foreground"></span>
			</button>
			<button @click="route_to_page('next')" class="project__navigation-next project__navigation-button">
				<span class="project__navigation-text">next project</span>
				<span class="project__navigation-foreground project__navigation-foreground--left"></span>
			</button>
		</div>
	</div>
	
</template>

<script>
	import Brand from '@/components/Brand.vue';
	import Navigation from '@/components/Navigation.vue';
	
	import Router from '../router';
	import gsap from 'gsap';

	export default {
		components: {
			 Navigation, Brand
		},
		name: 'Project',
		props: ['apploaded', 'preloader'],
		data(){
			return {
				home: '/',
				active_project: this.$route.params.id,
				projects: [
					{
						id: 0,
						title: 'Foodorator',
						description: `I am a big fan of cooking and plating, you know. And sometimes it happens that i have a bunch of food, a strong desire to make something, but i don't imagine what meal i want to cook today. So fooderator is an app that intended to solve this problem.It helps you to find a recipy from the ingredients you have. It's quite easy to use, you only have to specify the ingredient name and press enter. The ingredients can be added and removed in any order.`,
						technologies: ['VueJS', 'NodeJs', 'SASS', 'Pug', 'MongoDB'],
						year: '2021',
						services: ['Front-end', 'Backend'],
						portfolio: [
							{
								title: 'home page',
								images: ['foodorator_2.png', 'foodorator_1.png']
							},
						],
						link: 'https://github.com/The-Solomon/Foodorator'
					},
					

					



					{
						id: 1,
						title: 'Fisher.js',
						description: "Fisher is also one of my pet projects. Functionality: - \n-Generating chess board according to Forsyth–Edwards Notation (FEN).\n-Generation FEN string according to current game position\n-Validating pieces moves. ",
						technologies: ['VueJs', 'NodeJs', 'SASS', 'VueX'],
						year: '2021',
						services: ['Front-end', 'Backend'],
						link: 'https://github.com/The-Solomon/fisher.js'
					},

					{
						id: 2,
						title: 'Scraper',
						description: "Facebook scrapper is a console program based on Python3 which helps you to find facebook post with specified keywords and parametrs and to save it in .cvs file and/or display in console. It may be useful for finding job offers, appartment rent offers ets.",
						technologies: ['Python 3', 'Selenium'],
						year: '2021',
						services: ['Back-end'],
						link: 'https://github.com/The-Solomon/fb-parser'
					},
					
				]
			}
		},
		watch: {
			// When preloader is hidden - animate page entrance
			preloader: function(value) {
				if(value) { this.animate_first_entrance() }
			}
		},
		mounted(){
			this.set_page_title()
			if(this.preloader){ this.animate_first_entrance() }
			this.animate_on_scroll()
		},
		methods: {
			set_page_title(){
				document.title = 'Andrew Solomon | Portfolio'
			},
			animate_first_entrance(){
				let value_span = document.querySelectorAll('.value span')
				value_span.forEach((span) => {
					span.classList.add('active')
				})
				gsap.to('.animated-foreground', {left: '100%' , duration: 0.35, delay: 0})
				gsap.from('.project__title-text', {y: '120%' , duration: 0.35, delay: 0.6})
				gsap.from('.project__description-text', {y: '45%', opacity: 0, delay: 0.4, duration: 0.95})
				gsap.from('hr', {width: 0, delay: 1.5, duration: 0.65})

				gsap.from('.pw1', {opacity: 0, y: '55%', duration: 0.4, delay: 0.4})
				gsap.from('.pw2', {opacity: 0, y: '55%', duration: 0.4, delay: 0.5})
				gsap.from('.pw3', {opacity: 0, y: '55%', duration: 0.4, delay: 0.6})
				gsap.from('.pw4', {opacity: 0, y: '55%', duration: 0.4, delay: 0.7})

				// gsap.from('.value span', {y: '120%', duration: 0.4, delay: 0.7})
				// gsap.from('.value span', {y: '120%', duration: 0.4, delay: 0.8})
				// gsap.from('.value span', {y: '120%', duration: 0.4, delay: 0.9})
				// gsap.from('.value span', {y: '120%', duration: 0.4, delay: 1})
			},
			animate_on_scroll(){
				document.addEventListener('scroll', (e) => {
					let wrap = document.querySelectorAll('.project__image-wrap'),
						title = document.querySelectorAll('.animated-title');
					title.forEach((title) => {
						let title_text = title.querySelector('.project__page-title-text')		
						if(title.getBoundingClientRect().top < window.innerHeight / 100 * 60) {
							title_text.classList.add('project__page-title-text--active')
						}
					})


					wrap.forEach((item) => {
						let wrap_img = item.querySelector('img'),
							wrap_foreground = item.querySelector('.project__image-foreground');
						if(item.getBoundingClientRect().top < window.innerHeight / 100 * 60){
							wrap_foreground.classList.add('project__image-foreground--active')
							wrap_img.classList.add('_active')
						}
					})
				})
				
			},
			get_page(page){
				let projects_count = this.projects.length,
					current_page = this.active_project;
				if(page == 'next'){
					if(current_page < projects_count - 1){
					 return parseInt(current_page) + 1
					}

					return 0
				}
				if(page == 'prev'){
					if(current_page > 0) {
						return parseInt(current_page) - 1
					}
					return projects_count - 1
				}
			},
			route_to_page(direction){
				let page = direction == 'next' ?  this.get_page('next') : this.get_page('prev');
				Router.push({name: 'SingleProject', params: {id: page}})
			},
			set_page_title(){
				document.title = 'Andrew Solomon | Portfolio'
			}
		},
	}
</script>

