</template>
<script>
	import Navigation from '@/components/Navigation.vue'
	import Brand from '@/components/Brand.vue'
	
	export default {
		props: ['preloader'],
		components: {
			Brand, Navigation
		},
		data(){
			return {

			}
		},
		watch: {
			preloader: function(value) {
				if(value){ this.animate_first_entrance()}
			}
		},
		mounted(){
			this.set_page_title()
			if(this.preloader){ this.animate_first_entrance() }
			this.animate_onScroll()
		},
		methods: {
			set_page_title(){
				document.title = 'Andrew Solomon | About me'
			},
			animate_first_entrance(){
				let title = document.querySelectorAll('.about__title'),
					paragraph_text = document.querySelectorAll('.about__paragraph-text'),
					line = document.querySelectorAll('.line__text'),
					cv_foreground = document.querySelector('.about__cv-foreground'),
					hire_foreground = document.querySelector('.about__hire-foreground');
					hire_foreground.classList.add('active')
					cv_foreground.classList.add('active')
				title.forEach((item) => {
					let foreground = item.querySelector('.about__title-foreground'),
						text = item.querySelector('.about__title-text')
					if(foreground.getBoundingClientRect().top < window.innerHeight / 100 * 80){
						foreground.classList.add('about__title-foreground--active')
						text.classList.add('about__title-text--active')
					}
				})

				paragraph_text.forEach((paragraph) => {
					if(paragraph.getBoundingClientRect().top < window.innerHeight / 100 * 80){
						paragraph.classList.add('about__paragraph-text--active')
					}
				})

				line.forEach((item) => {
					if(item.getBoundingClientRect().top < window.innerHeight / 100 * 80){
						item.classList.add('line__text--active')
					}
				})

				
			},
			animate_onScroll(){
				document.addEventListener('scroll', () => {
					this.animate_first_entrance()
				})
			}
		}
	}
</script>

 
