<template>
  <div class="app">
      <Preloader :apploaded="app_loaded" :preloader="preloader_isHidden"></Preloader> 
      <router-view :key="$route.path" :apploaded="app_loaded" :preloader="preloader_isHidden"></router-view>
      <Footer></Footer>
      <Statistics></Statistics>
  </div>
</template>
<script>
import gsap from "gsap";
import Preloader from '@/components/Preloader.vue';
import Footer from '@/components/Footer.vue';
import Popup from '@/components/Popup.vue'
export default {
  components: {
    Preloader, Footer, Popup
  },
  name: 'app',
  data(){
    return {
      app_loaded: false,
	  preloader_isHidden: false,
	  popup_isHidden: true
    }
  },
  created(){
	  if (!this.get_ssid()) this.set_ssid();
  },
  mounted(){
	document.body.overflow = 'hidden'
	this.app_isLoaded()
	
  },
  methods: {
	
    get_ssid(){
      return localStorage.getItem('_ssid')
    },
    set_ssid(){
      let ssid = `_${(Date.now().toString(16) + Math.random().toString(16).substring  (2, 36)).toUpperCase()}`
      localStorage.setItem('_ssid', ssid)

      return true
    },
	app_isLoaded(){
		document.body.overflow = 'auto'
		this.app_loaded = true
	}
  }
}
</script>
<style lang="sass">
@import './assets/sass/style'
</style>
